import React from 'react'
import { graphql } from 'gatsby'
import MainLayout from '../layouts/MainLayout'
import TimelineView from '../views/TimelineView'

function CategoryTemplate({ data, pageContext }) {
    const {
        topics: { totalCount, group },
    } = data

    return (
        <MainLayout>
            <TimelineView title={pageContext.title} totalCount={totalCount} topics={group} />
        </MainLayout>
    )
}

export const query = graphql`
    query ($category: String!) {
        topics: allMarkdownRemark(filter: { fields: { collection: { eq: "topics" } }, frontmatter: { categories: { eq: $category } } }) {
            totalCount
            group(field: frontmatter___date) {
                fieldValue
                nodes {
                    html
                    frontmatter {
                        categories
                        date
                        icons
                        title
                        subtopics {
                            childMarkdownRemark {
                                html
                                frontmatter {
                                    title
                                    icons
                                }
                            }
                        }
                    }
                }
            }
        }
    }
`

export default CategoryTemplate
